body {
  padding: 0;
  margin: 0;
}
canvas {
  display: block;
  background-color: #000000;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.current-score {
  left: 20px;
}
.top-score {
  right: 20px;
}
.score {
  display: block;
  position: absolute;
  top: 10vh;
  z-index: 1;
  font-size: 14px;
}
.controls {
  display: block;
  position: absolute;
  top: 25vh;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  z-index: 1;
  font-size: 1.5rem;
  text-align: center;
  line-height: 1.6;
}
.endgame{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 16px;
  z-index: 1;
  font-size: 1.5rem;
  text-align: center;
}
.endgame-message {
  font-size: 2rem;
}
button{
  border: 4px solid #ffffff;
  background-color: transparent;
  color: #ffffff;
  font-size: 20px;
  padding: 10px 20px;
  margin: 10px;
  font-family: 'PT Mono', serif;
  cursor: pointer;
}
button:hover{
  background-color: #ffffff;
  color: #000000;
}

@media (min-width: 1000px) { 
    .score, .controls {
        top: 10vh;
    }

}

@media (max-width: 1000px) {
  .score, .controls {
    top: 18vh !important;
  }
}

.fifteen {
    text-align: center;
}

body, .fifteen button {
    font-size: 14pt;
    font-family: 'Roboto', sans-serif;
}

#puzzlearea {
    margin: 5rem auto auto auto;
    height: 800px;
    width: 800px;
    position: relative;
}

.square {
    -webkit-transition: .5s;
    transition: .5s;
    color: black;
    border: solid black 2px;
    height: 198px;
    width: 198px;
    position: absolute;
    font-size: 40pt;
    cursor: default;
    background-image: url("https://media.giphy.com/media/omAAeYvzPdVZK/giphy.gif");
    background-repeat: no-repeat;
    background-size: 800px 800px;
    border-radius: 3px;
    line-height: 185px;
}

.highlight {
    color: red;
    border: solid red 2px;
    cursor: pointer;
}

.solved {
    height: 200px;
    width: 200px;
    border: 0px;
    border-radius: 0px;
    font-size: 0px;
}

.fifteen label {
    color: white !important;
    margin: 0px auto auto 2rem;
}

.fifteen button {
    margin-left: 20%;
}

.won {
    position: absolute;
    top: 42%;
    left: 44%;
    background-color: azure;
    padding: 60px;
    box-shadow: 2px 2px 8px rgba(49,49,49,.5);
    border-radius: 5px;
}

label {
    font-size: 2rem !important;
    font-family: 'Roboto', sans-serif !important;
}

#changePic {
    font-weight: 500 !important;
    color: black !important;
}

.link {
    margin-left: 0px !important;
    padding-left: 0px !important;
}

.fifteen-buttons {
    display: flex;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 20px !important;
}
